.weekly-wrap {
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  display: flex;
}

.weekly-gallery {
  margin-top: 24px;
}

.weekly-gallery > .film-card:not(:first-child) {
  display: none;
}

.star, .star-half, .star-outline {
  width: 14px;
  height: 14px;
}

@media screen and (min-width: 320px) {
  .weekly-title {
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.9;
  }

  .weekly-link {
    color: var(--color-orange);
    font-size: 16px;
    font-weight: 500;
    line-height: 1.9;
    text-decoration: none;
  }
}

@media screen and (min-width: 768px) {
  .weekly-wrap {
    padding-top: 52px;
  }

  .weekly-title, .weekly-link {
    font-size: 24px;
    line-height: 1.17;
  }

  .weekly-list {
    display: flex;
  }

  .weekly-gallery > .film-card:not(:first-child) {
    display: block;
  }

  .weekly-gallery {
    gap: 16px;
    display: flex;
  }

  .star, .star-half, .star-outline {
    width: 10px;
    height: 10px;
  }
}

@media screen and (min-width: 1280px) {
  .weekly-wrap {
    padding-top: 80px;
  }

  .weekly-title {
